import { supabase } from "../../database/supabaseClient";

/**
 * Fetches and merges user data from:
 * A = user_member_v2
 * B = user_private_v2
 * C = user_credentials
 *
 * Priorities:
 * - Full name, title, field_of_expertise ‚Üí from A
 * - Contact info (email, phone, address, dob) ‚Üí from B or C
 * - Credentials (C) override only overlapping primitive fields
 */
export async function mergeUserData(userId: string, debug = false) {
  if (debug) console.log(`üöÄ mergeUserData() called for userId=${userId}`);

  const [aRes, bRes, cRes] = await Promise.all([
    supabase.from("user_member_v2").select("*").eq("id", userId).maybeSingle(),
    supabase.from("user_private_v2").select("*").eq("id", userId).maybeSingle(),
    supabase.from("user_credentials").select("*").eq("user_id", userId).maybeSingle(),
  ]);

  const a = aRes.data || {};
  const b = bRes.data || {};
  const c = cRes.data || {};

  if (debug) {
    console.group(`üìÇ Raw data for ${userId}`);
    console.log("A (user_member_v2):", a);
    console.log("B (user_private_v2):", b);
    console.log("C (user_credentials):", c);
    console.groupEnd();
  }

  /** ------------------------------------------------------------
   * Merge priorities
   * ------------------------------------------------------------ */
  const merged = {
    id: userId,

    // üß± Member fields (A)
    fullName: a.full_name ?? null,
    hometown: a.hometown ?? null,
    title: a.title ?? null,
    rank: a.rank ?? null,
    field: a.field_of_expertise ?? null,
    avatar: a.avatar_url ?? null,
    membership_level: a.membership_level ?? null,
    profile_status: a.profile_status ?? null,

    // üß± Private info (B) ‚Äì fallback to credentials (C)
    email: b.email ?? c.email ?? null,
    phone: b.phone ?? c.phone ?? null,
    address: b.address ?? null,
    dob: b.dob ?? null,
    goals: b.personal_goals ?? null,
    vision: b.future_vision ?? null,
    application_process: b.application_process ?? null,
    profile_progress: b.profile_progress ?? 0,
  };

  /** ------------------------------------------------------------
   * Optional: collision detector
   * ------------------------------------------------------------ */
  if (debug) {
    const allKeys = Array.from(
      new Set([...Object.keys(a), ...Object.keys(b), ...Object.keys(c)])
    );
    for (const key of allKeys) {
      const vals = { A: a[key], B: b[key], C: c[key] };
      const nonNull = Object.entries(vals).filter(
        ([, v]) => v !== undefined && v !== null && v !== ""
      );
      if (nonNull.length > 1) {
        console.log(
          `‚ö†Ô∏è Collision: "${key}" in ${nonNull
            .map(([src]) => src)
            .join(", ")} ‚Üí`,
          nonNull.map(([src, v]) => `${src}: ${JSON.stringify(v)}`).join(" | ")
        );
      }
    }

    console.group(`üß© Merge Summary for ${userId}`);
    console.table(
      Object.entries(merged).map(([col, val]) => ({
        column: col,
        A: a[col],
        B: b[col],
        C: c[col],
        chosen: val,
      }))
    );
    console.groupEnd();
    console.log("‚úÖ mergeUserData complete");
  }

  return merged;
}
