import { supabase } from "../../../database/supabaseClient";

export async function getUserSafe({
  debug = false,
  timeoutMs = 5000,
  redirectTo = "/sign-in",
}: {
  debug?: boolean;
  timeoutMs?: number;
  redirectTo?: string;
} = {}) {
  const log = (...args: any[]) => debug && console.log("[getUserSafe]", ...args);

  try {
    // 1Ô∏è‚É£ Try cached session first (fast, no network)
    const { data: sessionData } = await supabase.auth.getSession();
    const cachedUser = sessionData?.session?.user;
    if (cachedUser) {
      log("‚úÖ got user from cached session:", cachedUser.email);
      return cachedUser;
    }

    // 2Ô∏è‚É£ Timeout wrapper around getUser()
    const timeout = new Promise((_, reject) =>
      setTimeout(() => reject(new Error("‚è∞ auth.getUser() stalled")), timeoutMs)
    );
    const { data, error } = await Promise.race([
      supabase.auth.getUser(),
      timeout,
    ]) as any;

    if (error) throw error;
    if (data?.user) {
      log("‚úÖ got user via getUser()");
      return data.user;
    }

    // 3Ô∏è‚É£ Try refresh session if none
    log("üîÑ attempting session refresh‚Ä¶");
    const { error: refreshErr, data: refreshed } =
      await supabase.auth.refreshSession();

    if (!refreshErr && refreshed?.session?.user) {
      log("‚úÖ refreshed session, user:", refreshed.session.user.email);
      return refreshed.session.user;
    }

    throw new Error("‚ùå No valid user after refresh");
  } catch (err: any) {
    log("‚ö†Ô∏è auth error:", err.message);

    // 4Ô∏è‚É£ Self-repair path ‚Äî clear Supabase auth keys only
    try {
      await supabase.auth.signOut();
      Object.keys(localStorage)
        .filter((k) => k.startsWith("sb-") && k.includes("auth-token"))
        .forEach((k) => localStorage.removeItem(k));
      log("üßº cleared stale tokens");
    } catch {}

    // 5Ô∏è‚É£ Redirect (async safe)
    if (typeof window !== "undefined") {
      log("‚û°Ô∏è redirecting to sign-in");
      window.location.replace(`${redirectTo}?reason=session_repair`);
    }
    return null;
  }
}
