// src/members/dashboard/components/TimelineStep.tsx
import React, { memo } from 'react';
import { FiCheckCircle } from 'react-icons/fi';

const TimelineStep = memo(({ step, index, totalSteps }: any) => (
  <div className={`timeline-step ${step.completed ? 'completed' : ''} ${step.current ? 'current' : ''}`}>
    <div style={{ backgroundColor : '#001a33'}} className="step-marker">{step.completed ? <FiCheckCircle /> : step.step}</div>
    <div className="step-content"><h4>Step {step.step}</h4><p>{step.label}</p></div>
    {index < totalSteps - 1 && <div className="step-connector"></div>}
  </div>
));

export default TimelineStep;
