import { supabase } from '../../database/supabaseClient';

/* ============================================================
   ‚úÖ Safe Auto-Upsert + Update (Schema-aware)
   ============================================================ */
export async function updateUserProfile(payload: Record<string, any>) {
  try {
    console.log("üü° updateUserProfile called with:", payload);

    const {
      data: { session },
      error: sessionError,
    } = await supabase.auth.getSession();
    if (sessionError) throw sessionError;
    if (!session?.user) throw new Error("Not authenticated");

    const userId = session.user.id;
    console.log("üü¢ AUTH CHECK ‚Üí", { userId });

    // üß± Split payload into tables
    const memberPayload: Record<string, any> = {};
    const privatePayload: Record<string, any> = {};

    // --- user_member_v2 fields ---
    if (payload.fullName !== undefined) memberPayload.full_name = payload.fullName;
    if (payload.field !== undefined) memberPayload.field_of_expertise = payload.field;
    if (payload.hometown !== undefined) memberPayload.hometown = payload.hometown;
    if (payload.title !== undefined) memberPayload.title = payload.title;
    if (payload.rank !== undefined) memberPayload.rank = payload.rank;
    if (payload.avatar !== undefined) memberPayload.avatar_url = payload.avatar;
    if (payload.membership_level !== undefined)
      memberPayload.membership_level = payload.membership_level;
    if (payload.profile_status !== undefined)
      memberPayload.profile_status = payload.profile_status;

    // --- user_private_v2 fields ---
    if (payload.email !== undefined) privatePayload.email = payload.email;
    if (payload.phone !== undefined) privatePayload.phone = payload.phone;
    if (payload.address !== undefined) privatePayload.address = payload.address;
    if (payload.dob !== undefined) privatePayload.dob = payload.dob;
    if (payload.goals !== undefined) privatePayload.personal_goals = payload.goals;
    if (payload.vision !== undefined) privatePayload.future_vision = payload.vision;
    if (payload.application_status !== undefined)
      privatePayload.application_process = payload.application_status;
    if (payload.profile_progress !== undefined)
      privatePayload.profile_progress = payload.profile_progress;

    console.log("üü£ Split payloads ‚Üí", { memberPayload, privatePayload });

    /* ------------------------------------------------------------
       ‚úÖ MEMBER TABLE ‚Äî Update first, insert if missing
    ------------------------------------------------------------ */
    if (Object.keys(memberPayload).length > 0) {
      const { error: updateErr } = await supabase
        .from("user_member_v2")
        .update({ ...memberPayload, updated_at: new Date().toISOString() })
        .eq("id", userId);

      if (updateErr) {
        console.warn("üü† Update failed ‚Üí trying insert for member table:", updateErr.message);
        const { error: insertErr } = await supabase
          .from("user_member_v2")
          .insert({ id: userId, ...memberPayload });
        if (insertErr) throw insertErr;
      }
    }

    /* ------------------------------------------------------------
       ‚úÖ PRIVATE TABLE ‚Äî Same logic
    ------------------------------------------------------------ */
    if (Object.keys(privatePayload).length > 0) {
      const { error: updateErr } = await supabase
        .from("user_private_v2")
        .update({ ...privatePayload, updated_at: new Date().toISOString() })
        .eq("id", userId);

      if (updateErr) {
        console.warn("üü† Update failed ‚Üí trying insert for private table:", updateErr.message);
        const { error: insertErr } = await supabase
          .from("user_private_v2")
          .insert({ id: userId, ...privatePayload });
        if (insertErr) throw insertErr;
      }
    }

    console.log("‚úÖ Both tables updated successfully (no re-fetch)");
    return { success: true };
  } catch (error: any) {
    console.error("‚ùå updateUserProfile failed:", error.message);
    return { success: false, error };
  }
}

/* ============================================================
   ‚úÖ Data Loader (no change except uses correct field sources)
   ============================================================ */
export const loadUserBundle = async () => {
  const {
    data: { session },
    error: sessionError,
  } = await supabase.auth.getSession();
  if (sessionError) throw sessionError;
  const user = session?.user;
  if (!user) throw new Error("Not logged in");

  console.log("üü¢ Loading profile for:", user.id);

  const { data: privateData, error: privateErr } = await supabase
    .from("user_private_v2")
    .select("*")
    .eq("id", user.id)
    .maybeSingle();

  const { data: memberData, error: memberErr } = await supabase
    .from("user_member_v2")
    .select("*")
    .eq("id", user.id)
    .maybeSingle();

  if (privateErr) console.warn("‚ö†Ô∏è user_private_v2 error:", privateErr.message);
  if (memberErr) console.warn("‚ö†Ô∏è user_member_v2 error:", memberErr.message);

  const { data: logs } = await supabase
    .from("user_activity_log")
    .select("action, created_at")
    .eq("user_id", user.id)
    .order("created_at", { ascending: false })
    .limit(10);

  const step = privateData?.application_process || 1;

  const mapped = {
    fullName: memberData?.full_name || user.user_metadata?.full_name || "",
    hometown: memberData?.hometown || "",
    title: memberData?.title || "",
    rank: memberData?.rank || "",
    field: memberData?.field_of_expertise || "",
    avatar: memberData?.avatar_url || "",
    email: privateData?.email || user.email || "",
    phone: privateData?.phone || "",
    address: privateData?.address || "",
    dob: privateData?.dob || "",
    goals: privateData?.personal_goals || "",
    vision: privateData?.future_vision || "",
    application_status: privateData?.application_process || "",
  };

  console.log("üß≠ Final mapped data ‚Üí", mapped);

  return { user, mapped, logs: logs || [], step, memberData, privateData };
};
