// src/members/dashboard/components/ProfileField.tsx
import React, { memo } from 'react';
import { FiEdit, FiSave, FiX } from 'react-icons/fi';

interface ProfileFieldProps {
  field: {
    key: string;
    label: string;
    icon?: React.ReactNode;
  };
  userData: Record<string, any>;
  editingField: string | null;
  tempValue: string;
  onEditStart: (key: string, value: string) => void;
  onSave: (key: string) => void;
  onCancel: () => void;
  onTempValueChange: (val: string) => void;
  savingField?: string | null;
  savingSidebar?: boolean;
}

const ProfileField: React.FC<ProfileFieldProps> = memo(
  ({
    field,
    userData,
    editingField,
    tempValue,
    onEditStart,
    onSave,
    onCancel,
    onTempValueChange,
    savingField,
    savingSidebar
  }) => {
    const isEditing = editingField === field.key;
    const isSaving = savingField === field.key;

    // Determine input type dynamically
    const inputType =
      field.key === 'dob'
        ? 'date'
        : field.key === 'email'
        ? 'email'
        : 'text';

    return (
      <div className="profile-field">
        <label>
          {field.icon}
          {field.label}
        </label>

        <div className="field-value">
          {isEditing ? (
            <div className="edit-controls">
              <input
                type={inputType}
                value={tempValue || ''}
                onChange={(e) => onTempValueChange(e.target.value)}
                className="edit-input"
                autoFocus
              />
              <div className="edit-actions">
                <button
                  className="save-btn"
                  onClick={() => onSave(field.key)}
                  disabled={!!savingField || savingSidebar}
                >
                  {isSaving ? (
                    <span
                      className="spinner"
                      style={{
                        width: '16px',
                        height: '16px',
                        border: '2px solid #fff',
                        borderTop: '2px solid transparent',
                        borderRadius: '50%',
                        display: 'inline-block',
                        animation: 'spin 0.8s linear infinite'
                      }}
                    />
                  ) : (
                    <FiSave />
                  )}
                </button>

                <button
                  className="cancel-btn"
                  onClick={onCancel}
                  disabled={!!savingField || savingSidebar}
                >
                  <FiX />
                </button>
              </div>
            </div>
          ) : (
            <>
              <span>
                {field.key === 'dob' && userData[field.key]
                  ? new Date(userData[field.key]).toLocaleDateString()
                  : userData[field.key] || '‚Äî'}
              </span>
              <button
                className="edit-btn"
                onClick={() =>
                  onEditStart(field.key, userData[field.key] || '')
                }
                disabled={!!savingField || savingSidebar}
              >
                <FiEdit />
              </button>
            </>
          )}
        </div>
      </div>
    );
  }
);

export default ProfileField;
